.quiz-container{
	padding: 24px;
	width: 60%;
	margin: 24px auto;
	background-color: white;
  border-radius: 10px;
	display: flex;
	flex-direction: column;
}

.question-container {
	margin: 0 auto;
  background-color: white;
	width: 100%;
	padding: 13px 0;
	h3{
		color: #42948A;
		border-bottom: 2px solid #CCCCCC ;
		padding: 10px 0;
	}

}

.answers-container{
	flex-direction: column;
	gap: 6px;
	display: flex;
}

.answer-button {
	text-align: start;
	background-color: #CCCCCC;
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.correct {
  background-color: #377E22;
	color: white;
}

.incorrect {
  background-color: red;
	color: white;

}

.next-button {
	background-color: #429488;
	width: 75px;
	height: 32px;
	color: white;
	display: flex;
	align-items: center;
	justify-content: center;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.indicator-container {
	align-self: self-start;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.indicator {
	display: flex;
	justify-content: center;
	align-items: center;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin: 0 5px;
	background-color: #CCCCCC;
}

.indicator.correct {
  background-color: #377E22;
}

.indicator.incorrect {
  background-color: red;
}
.restart-btn{
	background-color: #429488;
	height: 32px;
	color: white;
	display: flex;
	align-items: center;
	justify-content: center;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
